import { COLOR_1, COLOR_2, COLOR_4,  COLOR_3 , COLOR_5 } from "constants/ChartConstant";
import React, { useState, useEffect } from "react";
import Chart from "react-apexcharts";

const initialState = {
  series: [],
  options: {
    plotOptions: {
      bar: {
        horizontal: false,
        columnWidth: "60%",
        endingShape: "rounded",
      },
    },
    colors: [COLOR_1, COLOR_2, COLOR_3, COLOR_4, COLOR_5],
    dataLabels: {
      enabled: false,
    },
    stroke: {
      show: true,
      width: 2,
      colors: ["transparent"],
    },
    xaxis: {
      categories: [],
    },
    fill: {
      opacity: 1,
    },
    tooltip: {
      y: {
        formatter: val => `${val}`,
      },
    },
  },
};

const BasicColumn = props => {
  const { categories, series } = props;
  const [chartData, setChartData] = useState(initialState);

  useEffect(() => {
    const data = {
      series: series ?? [],
      options: {
        plotOptions: {
          bar: {
            horizontal: false,
            columnWidth: "60%",
            endingShape: "rounded",
          },
        },
        colors: [COLOR_1, COLOR_2, COLOR_3, COLOR_4, COLOR_5],
        dataLabels: {
          enabled: false,
        },
        noData: {
          text: "No Data Found",
          align: 'center',
          verticalAlign: 'middle',
          offsetX: 0,
          offsetY: 0,
          style: {
            color: '#df4759',
            fontSize: '14px',
            fontFamily: undefined
          }
        },
        stroke: {
          show: true,
          width: 2,
          colors: ["transparent"],
        },
        xaxis: {
          categories: categories ?? [],
        },
        fill: {
          opacity: 1,
        },
        tooltip: {
          y: {
            formatter: val => `${val}`,
          },
        },
      },
    };
    setChartData(data);
  }, [categories, series]);

  return <Chart options={chartData.options} series={chartData.series} height={300} type="bar" />;
};

export default BasicColumn;
